.register-con {
  padding-top: 80px;
  padding-bottom: 80px;
}
.main-reg {
  background-color: #fff;
  width: 672px;
  margin: 0 auto;
  overflow: hidden;
}
.main-reg .main-tit {
  font-size: 24px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
  text-align: center;
  margin-top: 48px;
  margin-bottom: 32px;
}
.main-reg .main-tabs {
  width: 484px;
  margin: 0 auto;
  padding-bottom: 30px;
}
.main-reg .ant-tabs-tab {
  font-size: 18px;
}
.main-reg .login-form-button {
  width: 100%;
  height: 60px;
  font-size: 18px;
}
.main-reg .ant-input-affix-wrapper,
.main-reg .ant-select-selection--single,
.main-reg .ant-select-dropdown-menu-item {
  height: 60px;
}
.main-reg .search-Button {
  width: 139px;
}
.main-reg .ant-input-search-button {
  height: 60px;
}
.main-reg .ant-select-selection-selected-value,
.main-reg .ant-select-dropdown-menu-item {
  line-height: 60px;
}
.main-reg .margin-b {
  margin-bottom: 9px;
}
.main-reg .operation {
  width: 100%;
  background-color: yellow;
}
.main-reg .operation div {
  display: inline-block;
}
.main-reg .forget-password {
  float: left;
  margin-left: 22px;
}
.main-reg .forget-register {
  float: left;
}
.main-reg .ant-tabs-content {
  margin-bottom: 0%;
}
@media only screen and (max-width: 768px) {
  .register-con {
    padding-top: 30px;
    padding-bottom: 0px;
  }
  .main-reg {
    background-color: #fff;
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
  }
  .main-reg .main-tit {
    font-size: 24px;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.85);
    text-align: center;
    margin-top: 24px;
    margin-bottom: 10px;
  }
  .main-reg .main-tabs {
    width: 80%;
    margin: 0 auto;
  }
  .main-reg .login-form-button {
    width: 100%;
    height: 100%;
    font-size: 18px;
  }
  .main-reg .ant-input-affix-wrapper,
  .main-reg .ant-select-selection--single,
  .main-reg .ant-select-dropdown-menu-item {
    height: 100%;
  }
  .main-reg .search-Button {
    width: 116px;
  }
  .main-reg .ant-input-search-button {
    height: 32px;
    width: 116px;
  }
  .main-reg .ant-select-selection-selected-value,
  .main-reg .ant-select-dropdown-menu-item {
    line-height: 100%;
  }
  .main-reg .ant-tabs-content {
    margin-bottom: 10%;
  }
  .main-reg .ant-select-selection-selected-value {
    line-height: 30px !important;
  }
}
